@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
body {
  margin: 0;
}

* {
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

*::-webkit-scrollbar {
  background-color: transparent;
}

img {
  max-width: 100%;
}

.inbox_people {
  background: #f8f8f8 none repeat scroll 0 0;
  overflow: hidden;
  max-width: 22rem;
  border-right: 1px solid #c4c4c4;
}

.inbox_msg {
  clear: both;
  overflow: hidden;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}

.context-menu {
  position: absolute;
  z-index: 999;
}

.menu {
  z-index: 9999999999;
  margin: 0;
  padding: 0;
  background: white;
  -webkit-box-shadow: 0px 0px 5px #00000011;
          box-shadow: 0px 0px 5px #00000011;
  border-radius: 0.5rem;
  font-size: 14px;
  line-height: 15px;
  border: 2px solid #0000001a;
}

.menu > li > a {
  font: inherit;
  border: 0;
  padding: 10px 18px 10px 15px;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  text-align: center;
  text-decoration: unset;
  color: #000;
  font-size: 0.8rem;
  font-weight: 500;
  -webkit-transition: 0.1s ease-in-out;
  transition: 0.1s ease-in-out;
}

.menu > li:first-child {
  border-bottom: 2px dashed #50505038;
}

.menu > li {
  width: 100%;
}

.menu > li > a:hover {
  background: #f1f3f7;
  color: #4b00ff;
  cursor: pointer;
}

.menu > li > a > i {
  padding-right: 10px;
}

ul {
  list-style: none;
}

.menu > li.trash > a:hover {
  color: red;
}

.menu > li.copy > a:hover {
  color: #04728f;
}

.top_spac {
  margin: 20px 0 0;
}

.recent_heading {
  float: left;
  width: 40%;
}

.srch_bar {
  display: inline-block;
  text-align: right;
  width: 60%;
}

.srch_bar .stylish-input-group .search-bar {
  padding: 0 2rem 0.1rem 0.5rem;
}

.headind_srch {
  padding: 1rem;
  overflow: hidden;
  border-bottom: 1px solid #c4c4c4;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.recent_heading h4 {
  color: #05728f;
  font-size: 1.2rem;
  margin: auto;
}

.srch_bar input {
  border-width: 0;
  width: 100%;
  padding: 2px 0 4px 6px;
  background: none;
}

.srch_bar input:focus {
  outline: none;
}

.srch_bar .input-group-addon button {
  background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
  border: medium none;
  padding: 0;
  color: #707070;
  font-size: 18px;
  cursor: pointer;
  -webkit-transform: scale(0.8) !important;
          transform: scale(0.8) !important;
}

.srch_bar .input-group-addon button:focus-visible {
  outline: none;
}

.srch_bar .input-group-addon {
  margin: 0 0 0 -27px;
}

.chat_ib h5 {
  font-size: 15px;
  color: #464646;
  margin: 0 0 8px 0;
}

.chat_ib h5 span {
  font-size: 13px;
  float: right;
}

.chat_ib p {
  font-size: 14px;
  color: #989898;
  margin: auto;
}

.chat_img {
  float: left;
  width: 2rem;
}

.chat_img img {
  width: 100%;
  height: 100%;
  border-radius: 5rem;
}

.chat_ib {
  float: left;
  padding: 0 0 0 15px;
  width: 88%;
}

.chat_people {
  clear: both;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}

.chat_list {
  border-bottom: 1px solid #c4c4c4;
  margin: 0;
  padding: 1rem;
}

.inbox_chat {
  width: 100%;
  padding: 0 !important;
  padding-right: 0;
  cursor: pointer;
}

.active_chat {
  background: #ebebeb;
}

.incoming_msg {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  margin: 12px 0 12px 0;
}

.incoming_msg_img {
  display: inline-block;
  -webkit-filter: drop-shadow(2px 2px 1px rgba(0, 0, 0, 0.209));
          filter: drop-shadow(2px 2px 1px rgba(0, 0, 0, 0.209));
  width: 38.594px;
}

.incoming_msg_img img {
  width: 100%;
  height: 100%;
  border-radius: 5rem;
}

.received_msg {
  display: inline-block;
  padding: 0 0 0 10px;
  vertical-align: top;
  width: 92%;
}

.received_withd_msg p {
  background: #ebebeb;
  border-radius: 0.6rem;
  font-size: 14px;
  line-height: 23px;
  -webkit-filter: drop-shadow(2px 2px 1px rgba(0, 0, 0, 0.209));
          filter: drop-shadow(2px 2px 1px rgba(0, 0, 0, 0.209));
  overflow-wrap: break-word;
  margin: 0;
  padding: 5px 10px 5px 12px;
}

.last_msg {
  white-space: nowrap;
  overflow: hidden !important;
  text-overflow: ellipsis;
}

.time_date {
  color: #7474748e;
  display: block;
  font-size: 0.7rem;
  text-align: end;
  margin: 3px 0 0;
  max-height: 16px;
}

.received_withd_msg {
  line-height: 1.6rem;
  display: inline-block;
  max-width: 70%;
}

.received_withd_msg::first-letter {
  text-transform: uppercase;
}

.messages {
  float: left;
  padding: 0 10px;
  width: 100%;
  -webkit-box-shadow: inset 0px 1px 4px 0px #08080821;
          box-shadow: inset 0px 1px 4px 0px #08080821;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.sent_msg p {
  background: #05728f;
  border-radius: 0.6rem;
  font-size: 14px;
  -webkit-transition: 0.5s all ease-in-out;
  transition: 0.5s all ease-in-out;
  -webkit-filter: drop-shadow(2px 2px 1px rgba(0, 0, 0, 0.209));
          filter: drop-shadow(2px 2px 1px rgba(0, 0, 0, 0.209));
  margin: 0;
  color: #fff;
  padding: 5px 10px 5px 12px;
}

.sent_msg p:hover {
  background: #156f88;
}

.outgoing_msg {
  overflow: hidden;
  margin: 12px 0 12px;
}

.sent_msg {
  float: right;
  max-width: 92%;
  word-break: break-all;
}

.type_msg {
  border-top: 1px solid #c4c4c4;
  width: 100%;
  background-color: white;
  position: relative;
  padding: 0.5rem 0.5rem 0 0.5rem;
}

.msg_send_btn {
  background: #05728f none repeat scroll 0 0;
  border: medium none;
  border-radius: 15rem;
  color: #fff;
  cursor: pointer;
  font-size: 20px;
  height: 3rem;
  position: absolute;
  right: 2rem;
  padding-bottom: 2px;
  padding-right: 8px;
  top: 18px;
  width: 3rem;
  -webkit-transition: all 0.1 ease-in-out;
  transition: all 0.1 ease-in-out;
}

.msg_send_btn:focus-visible {
  outline: none;
  border: none;
  -webkit-box-shadow: none;
  box-shadow: none;
}

.msg_send_btn:hover {
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
}

.fa-search:focus-visible {
  outline: none;
  border: none;
  -webkit-box-shadow: none;
  box-shadow: none;
}

.ContactList_image {
  height: 2rem;
  width: 2rem;
  border-radius: 10rem;
  background-position: center;
  background-size: cover;
}

.msg_history {
  height: 100%;
  width: 100%;
  overflow-y: auto;
}

.write_msg {
  background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
  border: medium none;
  color: #4c4c4c;
  font-size: 15px;
  height: 2.5rem;
  padding: 0;
  resize: none;
  width: 100%;
  z-index: 9999;
}

.write_msg:hover {
  cursor: text !important;
}

.write_msg:focus {
  outline: none;
  border: none;
}

.input_msg_write {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  padding-right: 1rem;
}

.msgSendButton {
  font-size: 1.2rem;
  color: #096c88;
  margin-bottom: 0.8rem;
}

.msgSendButton:hover {
  cursor: pointer;
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}

.msgSendButton:active {
  -webkit-transform: scale(0.96);
          transform: scale(0.96);
}

.messaging {
  height: 100vh;
}

@media (max-width: 860px) {
  .recent_heading {
    display: none;
  }
  .srch_bar {
    text-align: center;
    width: 100%;
  }
}

@media (max-width: 814px) {
  .srch_bar input {
    border: none !important;
  }
  .headind_srch {
    padding: 0.5rem;
  }
}

@media (max-width: 730px) {
  .inbox_people .headind_srch,
  .inbox_people .chat_ib,
  .inbox_people .srch_bar {
    display: none;
  }
  .inbox_people {
    width: 6rem;
  }
}

@media (max-width: 590px) {
  .inbox_people {
    width: 100%;
    max-width: 100%;
    -webkit-box-shadow: 0 1px 8px 0px #0808085e;
            box-shadow: 0 1px 8px 0px #0808085e;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999999999;
  }
  .inbox_chat {
    width: 79px;
  }
  .msg_history {
    padding-top: 4rem;
  }
  .chat_list {
    border-right: 1px solid #c4c4c4;
  }
}
